import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { ReactComponent as Standing19 } from "./assets/art/svg/human/standing-19.svg";
import Typography from "@material-ui/core/Typography";
import QuestionGroup from "./Question.jsx";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3, 2)
  },
  formControl: {
    margin: theme.spacing(3)
  },
  container: {
    width: "100%",
    height: "100%",
    marginL: "auto auto",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  human: {
    height: "50vh",
    maxWidth: "100ch",
    maxHeight: "100ch"
  }
}));
const questions = {
  age: [
    { id: "1", label: "over 65" },
    { id: "2", label: "less than 65" }
  ]
};
export default function FullWidthGrid() {
  const classes = useStyles();

  return (
    <div className={classes.container}>
      <Grid container spacing={1}>
        <Grid item xs={12} sm={6}>
          <div>
            <div className={classes.container}>
              <Standing19 className={classes.human} />
            </div>
          </div>
        </Grid>
        <Grid item xs={12} sm={6}>
          <div>
            <div className={classes.container}>
              <Paper className={classes.paper}>
                <Typography variant="h5" component="h3">
                  What's your age group?
                </Typography>
                <QuestionGroup items={questions["age"]} />
              </Paper>
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  );
}
